<template>
    <div class="blogList">
      <section>
        <ul>
          <template v-for="item in articleList">
          <li class="listItem">
            <div class="timeContent">
              <time :datetime="item.date">{{item.date}}</time>
            </div>
            <router-link :to="'article/'+item.source">
              <span class="blogTitleContent">{{item.title}}</span>
            </router-link>
          </li>
          </template>
        </ul>
      </section>
    </div>
</template>

<script>
    export default {
        data() {
          let articleList = this.$store.state.article.articleList;
          return{
            articleList
          }
        },
        methods: {

        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.blogList{
  width:40%;
  margin:0 auto;
  color:#c9cacc;

  .listItem{
    font-size:0.16rem;
    text-align: left;
    margin-bottom:0.1rem;
    .timeContent{
      display:inline-block;
      width:1rem;
      margin-right:0.1rem;
      color:#666;
    }
    .blogTitleContent{
      text-decoration:underline
    }
  }
}
</style>



